'use strict';

function _interopDefault (ex) { return (ex && (typeof ex === 'object') && 'default' in ex) ? ex['default'] : ex; }

var deepEqual = _interopDefault(require('dequal'));

const tryCatch = (call, msg) => {
  try {
    call();
  } catch (err) {
    throw new Error(msg(err));
  }
};

const _addListeners = (elem, listeners) => {
  tryCatch(
    () => {
      Object.entries(listeners).forEach(([key, value]) => {
        elem.addEventListener(key, value);
      });
    },
    (err) => `componentize > adding event listeners to ${elem.id} failed with message: ${err.message}`,
  );
};

const throwIf = (test, msg) => {
  if (test) {
    throw new Error(msg);
  }
};

const getElem = (doc, id) => {
  const elem = doc.getElementById(id);

  throwIf(
    !elem,
    `setComponent > doc.getElementById('${id}') returned ${elem}.`,
  );

  return elem;
};

const addListeners = (doc, id, listeners) => {
  const elem = getElem(doc, id);

  _addListeners(elem, listeners);
};

/* eslint no-param-reassign: ["error", { "props": false }] */


const render = (elem, view, data) => {
  tryCatch(
    () => { elem.innerHTML = view(data); },
    (err) => `componentize > view(data) failed with message: ${err.message}`,
  );
};

const recursiveRender = (elem, view, data, merge) => (update) => {
  const merged = merge(data, update);

  if (!deepEqual(data, merged)) {
    render(elem, view, merged);
  }

  return recursiveRender(elem, view, merge, merged);
};

const setMergeable = (
  doc,
  id,
  view,
  data = {},
  listeners = {},
  merge = (a, b) => ({ ...a, ...b }),
) => {
  const elem = getElem(doc, id);

  render(elem, view, data);
  _addListeners(elem, listeners);

  return recursiveRender(elem, view, data, merge);
};

const setStatic = (
  doc,
  id,
  view,
  data = {},
  listeners = {},
) => {
  const elem = getElem(doc, id);

  render(elem, view, data);
  _addListeners(elem, listeners);
};

const setUpdatable = (
  doc,
  id,
  view,
  data = {},
  listeners = {},
) => {
  const elem = getElem(doc, id);

  render(elem, view, data);
  _addListeners(elem, listeners);

  return (update) => render(elem, view, update);
};

var index = {
  setUpdateable: setUpdatable,
  addListeners,
  setMergeable,
  setStatic,
  setUpdatable,
};

module.exports = index;
